footer {
  text-align: center;
  // background-color: #1c1d25;
  background: linear-gradient(180deg, rgba(220,220,220,1) 0%, rgba(28,29,37,1) 100%);
  color: white;
  padding: 40px;
} 

.form:hover {
  box-shadow: none !important;
}

.contact {

  &__me {
    padding-bottom: 40px;
  }
}

.notification {
    font-size: 14px;
    position: fixed;
    z-index: 10;
    bottom: 1rem;
    right: 1rem;
}
  
.toastNotice{
  margin-bottom: 1rem;
  border-radius: 4px;
  box-shadow: 0 0 10px #999;
  color: #fff;
  opacity: .9;
  padding: 20px;
  transition: .3x ease;
}
  
.toastNotice:hover {
  box-shadow: 0 0 12px;
  opacity: 1;
}
  
.popin {
  height: 80px;
  width: 365px;
  color: white;
  padding: 20px;
  animation: toastRight .7s;
}
  
.popout {
  animation: toastLeft .7s;
}

.notifClose {
  float: right;
  background: none;
  border: none;
  color: white;
  opacity: .8;
  cursor: pointer;
}

.notificationTitle {
  font-weight: 700;
  font-size: 1rem;
  text-align: left;
  margin-top: 0;
  margin-bottom: 6px;
  width: 300px;
  height: 18px;
}

.notificationDesc {
  margin: 0;
  text-align: left;
}

.contactUs {
  min-height: 37vh;
}

.contact {
  animation: popIn 2000ms;
}

@keyframes popIn {
  0% {
      transform: scale(0);
  }
  80% {
    transform: scale(1.1);
  }
  90% {
    transform: scale(.9);
  }
  95% {
    transform: scale(1.05);
  }
  100% {
      transform: scale(1);
  }
}